    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: -998;
}
#slvj-window {
    width: 100%;
    min-height: 100%;
    z-index: 9999999999;
    background: rgba(0, 0, 0, 0.8);
    position: fixed;
    top: 0;
    left: 0;
}
#slvj-window,
#slvj-window * {
    box-sizing: border-box;
}
#slvj-background-close {
    width: 100%;
    min-height: 100%;
    z-index: 31;
    position: fixed;
    top: 0;
    left: 0;
}
#slvj-back-lightbox {
    max-width: 940px;
    max-height: 529px;
    z-index: 32;
    text-align: center;
    margin: 0 auto;
}
.slvj-lightbox {
    width: 100%;
    height: 100%;
    margin: 0;
    z-index: 53;
}
#slvj-close-icon:before {
    font-family: "fontawesome";
    color: #fff;
    content: '\f00d';
    font-size: 22px;
    opacity: 0.5;
    transition: all 0.3s ease-out;
    z-index: 99999;
    height: 22px;
    width: 22px;
    position: absolute;
    top: 15px;
    right: 15px;
    cursor: pointer;
}
#slvj-close-icon:before:hover {
