/* --------- 6.7 Pricing Table ---------- */
.pricing-block-content {
    background: #fff;
    padding: 25px 0 25px 0;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.07);
    transition: all 0.2s ease-in-out 0s;
    position: relative;
    border: 10px solid #fff;
}
.pricing-block ul {
    list-style: none;
    margin: 25px 0 25px 0;
}
.pricing-block li {
    padding: 14px 0;
    border-bottom: 1px dotted #e1e1e1;
}
.pricing-block li:last-of-type {
    border:none;
}
.pricing-block h3 {
    font-size:17px;
    text-transform:uppercase;
    padding-bottom:0;
}
.pricing-sub {
    font-style: italic;
    color:#ccc;
    margin:0 0 25px 0;
}
/*Price*/
.pricing {
    background: #f5f5f5;
    padding: 25px;
    position: relative;
}
.price {
    display: inline-block;
    font-size: 62px;
    font-weight: 700;
    position: relative;
}
.price span {
    position: absolute;
    top: 0;
    left: -20px;
    font-size: 22px;
}
.pricing  p {
    font-style:italic;
    color:#b4b4b4;
    line-height:auto;
    margin:0;
}
/*------------------------------------------------------------------------------------------*/
/* 7. Footer */
/*------------------------------------------------------------------------------------------*/
#landing-footer {
    clear: both;
}
#landing-footer .row {
    padding: 0;
}
#landing-footer p {
    margin: 0;
}
#landing-footer ul {
    list-style: none;
    text-align: right;
}
#landing-footer li {
    display: inline-block;
}
/* Scroll Up */
#scrollUp {
    bottom: 15px;
    right: 30px;
    width: 70px;
    height: 80px;
    margin-bottom: -10px;
    padding: 0 5px 20px 5px;
    text-align: center;
    text-decoration: none;
    color: #fff;
    transition: margin-bottom 150ms linear;
}
#scrollUp:hover {
    margin-bottom: 0;
}
#scrollUp:before {
    background: #ccc;
    font-family: "fontawesome";
    font-size: 2.4em;
    font-style: normal;
    font-weight: normal;
    text-align: center;
    color: #fff;
    width: 45px;
    height: 45px;
    display: inline-block;
    line-height: 45px;
    content: "\f106";
    opacity: 1;
}
/*Preloader*/
#preloader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff; /* change if the mask should have another color then white */
    z-index: 99999; /* makes sure it stays on top */
}
#status {
    width: 110px;
    height: 110px;
    position: absolute;
    left: 50%; /* centers the loading animation horizontally one the screen */
    top: 50%; /* centers the loading animation vertically one the screen */
    background-position: center;
    margin: -55px 0 0 -55px; /* is width and height divided by two */
    text-align: center;
}
